import React, { useState, useEffect } from "react";
import { TextField, Box, Typography, IconButton, Select, MenuItem, FormControl, InputLabel } from "@mui/material";
import { Grid } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from "@mui/icons-material/Remove";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import Calculadora from "./Calculadora";

function TarjetasHelper() {
    const [cuponNumber, setCuponNumber] = useState("");
    const [terminal, setTerminal] = useState("39274590");
    const [loteNumber, setLoteNumber] = useState("");

    // Load saved values from localStorage when component mounts
    useEffect(() => {
        const today = new Date().toISOString().split('T')[0];
        const savedCuponCounter = localStorage.getItem('cuponCounter') || "0001";
        const savedTerminal = localStorage.getItem('terminal') || "39274590";
        const savedLoteInfo = JSON.parse(localStorage.getItem('loteInfo') || '{"date":"", "counter":"001"}');
        
        // Format current date for cupon number
        const year = new Date().getFullYear().toString();
        const month = (new Date().getMonth() + 1).toString().padStart(2, '0');
        const day = new Date().getDate().toString().padStart(2, '0');
        
        setCuponNumber(`${year}${month}${day}${savedCuponCounter}`);
        setTerminal(savedTerminal);
        
        // Reset lote counter if it's a new day
        if (savedLoteInfo.date !== today) {
            setLoteNumber("001");
            localStorage.setItem('loteInfo', JSON.stringify({date: today, counter: "001"}));
        } else {
            setLoteNumber(savedLoteInfo.counter);
        }
    }, []);

    const incrementCuponCounter = () => {
        const datePrefix = cuponNumber.substring(0, 8);
        let counter = parseInt(cuponNumber.substring(8), 10);
        counter = (counter + 1) % 10000;
        const newCounter = counter.toString().padStart(4, '0');
        
        setCuponNumber(`${datePrefix}${newCounter}`);
        localStorage.setItem('cuponCounter', newCounter);
    };

    const decrementCuponCounter = () => {
        const datePrefix = cuponNumber.substring(0, 8);
        let counter = parseInt(cuponNumber.substring(8), 10);
        counter = (counter - 1 + 10000) % 10000;
        const newCounter = counter.toString().padStart(4, '0');
        
        setCuponNumber(`${datePrefix}${newCounter}`);
        localStorage.setItem('cuponCounter', newCounter);
    };

    const incrementLoteCounter = () => {
        const today = new Date().toISOString().split('T')[0];
        let counter = parseInt(loteNumber, 10);
        counter = (counter + 1) % 1000;
        const newCounter = counter.toString().padStart(3, '0');
        
        setLoteNumber(newCounter);
        localStorage.setItem('loteInfo', JSON.stringify({date: today, counter: newCounter}));
    };

    const decrementLoteCounter = () => {
        const today = new Date().toISOString().split('T')[0];
        let counter = parseInt(loteNumber, 10);
        counter = (counter - 1 + 1000) % 1000;
        const newCounter = counter.toString().padStart(3, '0');
        
        setLoteNumber(newCounter);
        localStorage.setItem('loteInfo', JSON.stringify({date: today, counter: newCounter}));
    };

    const handleTerminalChange = (event) => {
        setTerminal(event.target.value);
        localStorage.setItem('terminal', event.target.value);
    };

    const copyToClipboard = (text, increment = false) => {
        navigator.clipboard.writeText(text)
            .then(() => {
                console.log('Copied to clipboard: ' + text);
                if (increment) {
                    if (text === cuponNumber) {
                        incrementCuponCounter();
                    } else if (text === loteNumber) {
                        incrementLoteCounter();
                    }
                }
            })
            .catch(err => {
                console.error('Failed to copy: ', err);
            });
    };

    return (
        <Box sx={{ p: 2 }}>
            {/* First Row - Cupón Number */}
            <Grid container spacing={2} alignItems="center" sx={{ mb: 1 }}>
                <Grid item xs={8}>
                    <TextField
                        label="Número de Cupón"
                        value={cuponNumber}
                        onChange={(e) => setCuponNumber(e.target.value)}
                        fullWidth
                        margin="dense"
                        size="small"
                    />
                </Grid>
                <Grid item xs={4} sx={{ display: "flex", justifyContent: "flex-start" }}>
                    <IconButton onClick={incrementCuponCounter} color="primary" size="small">
                        <AddIcon />
                    </IconButton>
                    <IconButton onClick={decrementCuponCounter} color="secondary" size="small">
                        <RemoveIcon />
                    </IconButton>
                    <IconButton onClick={() => copyToClipboard(cuponNumber, true)} color="info" size="small">
                        <ContentCopyIcon />
                    </IconButton>
                </Grid>
            </Grid>

            {/* Second Row - Terminal */}
            <Grid container spacing={2} alignItems="center" sx={{ mb: 1 }}>
                <Grid item xs={8}>
                    <FormControl fullWidth margin="dense" size="small">
                        <InputLabel>Terminal</InputLabel>
                        <Select
                            value={terminal}
                            onChange={handleTerminalChange}
                            label="Terminal"
                        >
                            <MenuItem value="39274590">39274590</MenuItem>
                            <MenuItem value="12345678">12345678</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>
                <Grid item xs={4} sx={{ display: "flex", justifyContent: "flex-start" }}>
                    <IconButton onClick={() => copyToClipboard(terminal)} color="info" size="small">
                        <ContentCopyIcon />
                    </IconButton>
                </Grid>
            </Grid>

            {/* Third Row - Lote Number */}
            <Grid container spacing={2} alignItems="center" sx={{ mb: 1 }}>
                <Grid item xs={8}>
                    <TextField
                        label="Número de Lote"
                        value={loteNumber}
                        onChange={(e) => setLoteNumber(e.target.value)}
                        fullWidth
                        margin="dense"
                        size="small"
                    />
                </Grid>
                <Grid item xs={4} sx={{ display: "flex", justifyContent: "flex-start" }}>
                    <IconButton onClick={incrementLoteCounter} color="primary" size="small">
                        <AddIcon />
                    </IconButton>
                    <IconButton onClick={decrementLoteCounter} color="secondary" size="small">
                        <RemoveIcon />
                    </IconButton>
                    <IconButton onClick={() => copyToClipboard(loteNumber, true)} color="info" size="small">
                        <ContentCopyIcon />
                    </IconButton>
                </Grid>
            </Grid>

        <hr></hr>
            <Calculadora />
        </Box>
    );
}

export default TarjetasHelper;
